<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12" v-if="0===state">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Reset Password</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="New password" name="password"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="password" clearable
              />
              <v-text-field label="Re-type new password" name="cnfpassword"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="cnfpassword" clearable
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" @click="onResetClick"
              :loading="loading" :disabled="!inputOK"
            >Send</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="1===state">
          <v-card-text>
            You've just reset your password. Now you can login using new
            password.
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn text color="primary" to="/login">Go to login screen</v-btn>
          </v-card-actions>
        </v-card>
        <v-card class="elevation-12" v-if="2===state">
          <v-card-text>
            <strong>Invalid token!!!</strong>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { resetPassword } from "@/lib/auth"

export default {
  name: "ResetPwd",
  data: () => ({
    password: "",
    cnfpassword: "",
    loading: false,
    state: 0
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.password.length) return false
        if (0 === this.cnfpassword.length) return false
        if (this.password !== this.cnfpassword) return false
        return true
      } catch {
        return false
      }
    },
  },
  methods: {
    onResetClick: async function() {
      this.loading = true
      let resp = await resetPassword(this.$route.params.token, this.password)
      this.loading = false
      if (200 === resp.code) {
        this.state = 1
      } else {
        this.state = 2
        console.log(resp)
      }
    }
  }
}
</script>

